<!--
	This is the tables page, it uses the dashboard layout in:
	"./layouts/Dashboard.vue" .
 -->


<template>
  <div>
    <a-card
      :bordered="false"
      class="header-solid mb-24"
      :bodyStyle="{ padding: 0, paddingTop: '16px' }"
    >
      <template #title>
        <a-row type="flex" :gutter="24">
          <a-col :span="24" :md="12">       <span>موظفين المتجر </span></a-col>
          <a-col :span="24" :md="12"><router-link to="new" class="font-bold text-dark"
          ><a-icon type="plus"
          /> إضافة عضو</router-link></a-col>
        </a-row>

      </template>
    </a-card>
    <a-card
      :bordered="false"
      class="header-solid mb-24"
      :bodyStyle="{ padding: 0, paddingTop: '16px' }"
    >
      <div class="mx-25">
        <a-row type="flex" :gutter="24">
          <a-col :span="24" :md="12">
            <a-select
              v-model="pageSize"
              @change="onPageSizeChange"
              style="width: 70px"
            >
              <a-select-option value="5">5</a-select-option>
              <a-select-option value="10">10</a-select-option>
              <a-select-option value="15">15</a-select-option>
              <a-select-option value="20">20</a-select-option>
              <a-select-option value="25">25</a-select-option>
            </a-select>
            <label for="" class="ml-10">entries per page</label>
          </a-col>
          <!-- <a-col :span="24" :md="12" class="text-right">
            <a-input-search
              placeholder="input search text"
              style="max-width: 200px"
              v-model="query"
              @change="onSearchChange"
            />
          </a-col> -->
          <ExportExcelSheetVueJs
              :sheetData="dataSource"
              :columns="columns1"
              :filename="`aaaaaa`"
              :sheetname="`wwwwww`"
          >
            Download
          </ExportExcelSheetVueJs>
        </a-row>
      </div>

      <a-table
        class="mt-20"
        :columns="columns"
        :data-source="dataSource"
        :pagination="pagination"
        :loading="loading"
        @change="handleTableChange"
      >
        <template slot="status" slot-scope="status">
          <a-button
            v-if="status == 'ACTIVE'"
            shape="circle"
            size="small"
            class="btn-status border-success mr-5"
          >
            <a-icon
              class="m-0 text-success"
              type="check"
              style="font-size: 10px"
            />
          </a-button>
          <a-button
            v-else-if="status == 'DISABLED'"
            shape="circle"
            size="small"
            class="btn-status border-muted mr-5"
          >
            <a-icon
              class="m-0 text-muted"
              type="undo"
              style="font-size: 10px"
            />
          </a-button>
          <a-button
            v-else-if="status == 'Canceled'"
            shape="circle"
            size="small"
            class="btn-status border-danger mr-5"
          >
            <a-icon
              class="m-0 text-danger"
              type="close"
              style="font-size: 10px"
            />
          </a-button>
          <span style="vertical-align: middle">{{ status }}</span>
        </template>
        <template slot="id" slot-scope="id">
          <router-link
            :to="'/admins/'+id"
            class="font-bold text-dark mr-5 px-5 py-5"
            name="edit"
            ><a-icon type="edit"
          /></router-link>
          <a class="font-bold text-danger px-5 py-5" @click="onDelete(id)"
            ><a-icon type="delete"
          /></a>
        </template>
      </a-table>
    </a-card>
  </div>
</template>

<script>
// import columns from "./columns.js";

const columns = [
  {
    title: "الاسم",
    dataIndex: "display_name",
    sorter: (a, b) => a.name.length - b.name.length,
    sortDirections: ["desc", "asc"],
  },
  {
    title: "اسم المستخدم",
    dataIndex: "user_login",
    sorter: (a, b) => a.username.length - b.username.length,
    sortDirections: ["desc", "asc"],
  },
  {
    title: "البريد الإلكتروني",
    dataIndex: "user_email",
    sortDirections: ["desc", "asc"],
    sorter: true,
  },
  {
    title: "PHONE",
    dataIndex: "phone[0].meta_value",
    sorter: true,
    sortDirections: ["desc", "asc"],
  },

  {
    title: "STATUS",
    dataIndex: "user_status",
    sorter: true,
    sortDirections: ["desc", "asc"],
    scopedSlots: { customRender: "status" },
  },
  {
    title: "ACTIONS",
    dataIndex: "ID",
    sorter: false,
    scopedSlots: { customRender: "id" },
  },
];
export default {
  name:"admin_index",
  components: {},
  data() {
    return {
      columns1 : [
        {
          label: "Student Name",
          field: "display_name",
        },
        {
          label: "Registration Number / PRN",
          field: "regno",
        },
      ],
      data : [
        {
          studentName: "Dhanesh Pawar",
          regno: 10,
        },
        {
          studentName: "Student two name",
          regno: 30,
        }
      ],
      dataSource: [],
      pageSize: 5,
      pagination: {
        current: 0,
        pageSize: 5,
      },
      columns,
      loading: false,
      selectedRowKeys: [],
    };
  },
  methods: {
    // Event handler for first table's size change.
    onPageSizeChange() {
      this.pagination.pageSize = parseInt(this.pageSize);
      this.fetch();
    },


    handleTableChange(pagination, filters, sorter) {
      this.pagination = {
        ...this.pagination,
        current: pagination.current,
      };
      this.fetch({
        pageSize: pagination.pageSize,
        page: pagination.current,
        sortField: sorter.field,
        sortOrder: sorter.order,
        ...filters,
      });
    },

    async fetch(params = {}) {
      this.loading = true;
      let filters = "";
      // eslint-disable-next-line no-undef
      await axios({
        url:
          "/admin/manager?page=" +
          this.pagination.current +
          "&limit=" +
          this.pagination.pageSize +
          "&sort[column]=" +
          (params.sortField || "id") +
          "&sort[order]=" +
          (params.sortOrder || "asc") +
          filters,
        method: "GET",
      }).then(
        (response) => {
          this.loading = false;
          response = response.data.data;
          console.log(response)
          this.dataSource = response.data;
          this.pagination = {
            ...this.pagination,
            total: response.total,
            current: response.current_page,
          };
        },
        (error) => {
          console.log(error);
        }
      );
    },

    async onDelete(id) {
      this.$swal({
        title: "Are you sure?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Delete",
        closeOnCancel: true,
      }).then((result) => {
       if (result.isConfirmed) {
          this.loading = true;
         // eslint-disable-next-line no-undef
          axios({
            url: "/manager/" + id,
            method: "DELETE",
          })
                  // eslint-disable-next-line no-unused-vars
            .then((resp) => {
              this.$swal("Deleted!", "success");
              this.loading = true;
              this.fetch();
            })
            .catch((err) => {
              this.loading = false;
              this.$swal("Failed!", err.response.data.message, "danger");
            });
        }
      });
    },
  },

  mounted() {
  //  console.log(this.pagination);
    this.fetch();
  },
};
</script>

